___CSS_LOADER_EXPORT___.push([module.id, `div.static-scope {
  background-color: #181818;
}
div.static-scope > div {
  position: absolute;
}
div.static-scope > div.static-scope-data {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  font-size: 0.75em;
  font-family: Consolas, monospace;
  overflow-x: auto;
}
div.static-scope > div.static-scope-data > .static-scope-channel {
  display: grid;
  position: relative;
  width: 1px;
  flex: 1 1 auto;
  grid-template-columns: repeat(auto-fill, 110px);
  grid-template-rows: repeat(auto-fill, 20px);
  grid-auto-flow: column;
  border-bottom: #505050 dashed 1px;
}
div.static-scope > div.static-scope-data > .static-scope-channel > .static-scope-cell {
  display: block;
  width: 110px;
  height: 20px;
  background-color: inherit;
}
div.static-scope > div.static-scope-data > .static-scope-channel > .static-scope-cell > span {
  display: inline-block;
  border: #606060 solid 1px;
  text-align: right;
  padding: 0px 1px;
}
div.static-scope > div.static-scope-data > .static-scope-channel > .static-scope-cell > span:first-child {
  width: 30px;
  color: #ffffa0;
}
div.static-scope > div.static-scope-data > .static-scope-channel > .static-scope-cell > span:last-child {
  width: 80px;
}
div.static-scope > div.static-scope-data > .static-scope-channel > .static-scope-cell.highlight > span {
  border-color: #ff8800;
  background-color: #552200;
}
div.static-scope > canvas.static-scope-canvas {
  position: absolute;
  height: 100%;
  width: 100%;
}
div.static-scope > .static-scope-ui-controller {
  display: flex;
  opacity: 0.4;
  transition: opacity 0.15s ease-in-out, background-color 0.15s ease-in-out;
}
div.static-scope:hover > .static-scope-ui-controller {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.5);
}
div.static-scope > .static-scope-default {
  position: relative;
  top: 20%;
}
div.static-scope .btn-overlay {
  font-size: 12px;
  border-color: transparent;
  border-radius: 0px;
  padding: 0rem 0.1rem;
  z-index: 3;
}
div.static-scope .btn-overlay.active {
  color: unset !important;
  background-color: unset !important;
  border-color: transparent transparent #f8f9fa transparent !important;
  border-bottom-color: #f8f9fa !important;
}
div.static-scope .btn-overlay.active:hover {
  color: #212529 !important;
  background-color: #f8f9fa !important;
  border-color: #f8f9fa !important;
}
div.static-scope .btn-overlay:focus {
  box-shadow: none !important;
}
div.static-scope .btn-overlay.btn-overlay-icon {
  width: 20px;
}`, "",{"version":3,"sources":["webpack://./src/StaticScope.scss"],"names":[],"mappings":"AACA;EACI,yBAAA;AAAJ;AACI;EACI,kBAAA;AACR;AACI;EACI,aAAA;EACA,YAAA;EACA,WAAA;EACA,sBAAA;EACA,iBAAA;EACA,gCAAA;EACA,gBAAA;AACR;AAAQ;EACI,aAAA;EACA,kBAAA;EACA,UAAA;EACA,cAAA;EACA,+CAAA;EACA,2CAAA;EACA,sBAAA;EACA,iCAAA;AAEZ;AADY;EACI,cAAA;EACA,YAAA;EACA,YAAA;EACA,yBAAA;AAGhB;AAFgB;EACI,qBAAA;EACA,yBAAA;EACA,iBAAA;EACA,gBAAA;AAIpB;AAHoB;EACI,WAAA;EACA,cAAA;AAKxB;AAHoB;EACI,WAAA;AAKxB;AAFgB;EACI,qBAAA;EACA,yBAAA;AAIpB;AACI;EACI,kBAAA;EACA,YAAA;EACA,WAAA;AACR;AACI;EACI,aAAA;EACA,YAAA;EACA,yEAAA;AACR;AAEQ;EACI,UAAA;EACA,oCAAA;AAAZ;AAGI;EACI,kBAAA;EACA,QAAA;AADR;AAGI;EACI,eAAA;EACA,yBAAA;EACA,kBAAA;EACA,oBAAA;EACA,UAAA;AADR;AAEQ;EACI,uBAAA;EACA,kCAAA;EACA,oEAAA;EACA,uCAAA;AAAZ;AACY;EACI,yBAAA;EACA,oCAAA;EACA,gCAAA;AAChB;AAEQ;EACI,2BAAA;AAAZ;AAEQ;EACI,WAAA;AAAZ","sourcesContent":["\ndiv.static-scope {\n    background-color: #181818;\n    & > div {\n        position: absolute;\n    }\n    & > div.static-scope-data {\n        display: flex;\n        height: 100%;\n        width: 100%;\n        flex-direction: column;\n        font-size: 0.75em;\n        font-family: Consolas, monospace;\n        overflow-x: auto;\n        & > .static-scope-channel {\n            display: grid;\n            position: relative;\n            width: 1px; // for Safari\n            flex: 1 1 auto;\n            grid-template-columns: repeat(auto-fill, 110px);\n            grid-template-rows: repeat(auto-fill, 20px);\n            grid-auto-flow: column;\n            border-bottom: #505050 dashed 1px;\n            & > .static-scope-cell {\n                display: block;\n                width: 110px;\n                height: 20px;\n                background-color: inherit;\n                & > span {\n                    display: inline-block;\n                    border: #606060 solid 1px;\n                    text-align: right;\n                    padding: 0px 1px;\n                    &:first-child {\n     