  display: flex;
  position: relative;
  flex-direction: column;
  margin: -15px -15px 10px -15px;
  background-color: #1e1e1e;
  color: whitesmoke;
}
.filemanager > .filemanager-label {
  display: flex;
  position: relative;
  flex: 0 0 auto;
  flex-direction: row;
  background-color: rgba(128, 128, 128, 0.2);
  height: 25px;
}
.filemanager > .filemanager-label > .filemanager-btn-expand {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  position: relative;
  width: 18px;
  transition: transform 0.1s ease 0s;
}
.filemanager > .filemanager-label > .filemanager-btn-expand.expanded {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  transform: rotate(45deg);
}
.filemanager > .filemanager-label > .filemanager-span-label {
  display: flex;
  position: relative;
  flex: 1 1 100%;
  font-size: 14px;
  height: 100%;
  align-items: center;
  cursor: pointer;
}
.filemanager > .filemanager-label > .filemanager-btn-new-file {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  position: absolute;
  right: 0;
  border: 0;
}
.filemanager > .filemanager-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.25);
  display: none;
  z-index: 1;
}
.filemanager > .filemanager-files {
  display: block;
  flex: 1 1 auto;
  overflow: auto;
}
.filemanager > .filemanager-files > .filemanager-file {
  display: flex;
  position: relative;
  flex-direction: row;
  height: 25px;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-filename {
  display: flex;
  position: relative;
  flex: 1 1 100%;
  padding-left: 20px;
  font-size: 14px;
  height: 100%;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-filename[contenteditable=true] {
  background-color: #1e1e1e;
  cursor: auto;
}
.filemanager > .filemanager-files > .filemanager-file .filemanager-btn-icon {
  display: none;
}
.filemanager > .filemanager-files > .filemanager-file:hover {
  background-color: rgba(255, 255, 255, 0.15);
}
.filemanager > .filemanager-files > .filemanager-file:hover .filemanager-btn-icon {
  display: inline-block;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-main {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  opacity: 0;
  position: absolute;
  display: block;
  z-index: 1;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-main.active {
  opacity: 1;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-main.active:hover {
  opacity: 1;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-main:hover {
  opacity: 0.5;
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-rename {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}
.filemanager > .filemanager-files > .filemanager-file > .filemanager-btn-delete {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}
.filemanager > .filemanager-files > .filemanager-file.selected {
  background-color: #094771;
}
.filemanager .filemanager-btn-icon {
  display: inline-block;
  background-color: transparent;
  border: 0;
  height: 100%;
  width: 25px;
  background-size: 16px;
  background-position: center center;
  background-repeat: no-repeat;
}
.filemanager .filemanager-btn-icon:focus {
  outline: none;
